footer {
  height: 90px;
  background-color: $color-light-gray;
  box-shadow: 0px -5px 40px #00000029;
}
.footer-left {
  display: flex;
  flex-direction: column;
}
.footer-right {
  display: flex;
}
h4 {
  color: $color-primary;
  font: normal normal 300 26px Raleway;
  margin-bottom: 6px;
}
.footer-left p {
  font-size: 15px;
  color: $color-gray;
}
.twitter, .fb {
  width: 24px;
  height: 24px;
  cursor: pointer;
  margin-left: 20px;
}
.twitter-picture, .fb-picture {
  width: 100%;
  height: 100%;
}
footer .container{
  justify-content: space-between;
}